import React, {Component} from 'react';
import data from '../data.json'

class Project extends Component {
  constructor() {
    super();
    this.state = {
      name: null,
      description: null,
      technologies: [],
      live_link: null,
      github_link: null,
      images: []
    }
  }

  getData = () => {
    const index = this.props.match.params.id;
    const projects = data.projects.map((project) => project );

    this.setState({
      name: projects[index].project_name,
      description: projects[index].description,
      technologies: projects[index].technologies.map((tech) => tech),
      live_link: projects[index].live_link,
      github_link: projects[index].github_link,
      images: projects[index].image_urls.map((img) => img)
    })
  }

  componentDidMount(){
    this.getData();
  }

  render() {

    return (
      <React.Fragment>
      <div class="off-canvas position-left reveal-for-large portfolio-me" id="my-info" data-off-canvas="">
        <div class="grid-y grid-padding-x" style={{height: '100%'}}><br />
            <div class="cell shrink"><img class="profileThumbnail" src={process.env.PUBLIC_URL + "/images/profile_pic.jpg"} alt="" /></div>
            <div class="cell auto">
                <h5>Judah</h5>
                <p class="text-light">Welcome to my portfolio.</p><a href="/about">Learn More →</a>
            </div>
          </div>
      </div>
      <div class="off-canvas-content" data-off-canvas-content>
        <div class="title-bar hide-for-large">
          <div class="title-bar-left">
            <button class="menu-icon" type="button" data-toggle="my-info"></button>
            <span class="title-bar-title">Judah Devasahayam</span>
          </div>
        </div>
      <nav class= "grid-container portfolio-breadcrumb">
        <a href="/index">← Back</a>
      </nav>
        <article class="grid-container portfolio-intro portfolio-project">
          <div class="grid-x">
            <div class="cell small-6 medium-9 large-9">
              <h1>{this.state.name}</h1>
              <p class="lead text-light">{this.state.description}</p>
            </div>
            <div class="cell portfolio-meta small-6 medium-2 large-3">
              <h6>Technologies</h6>
              <ul>
                {
                  this.state.technologies.map((technology) => {
                    return <li>{technology}</li>
                  })
                }
              </ul>
              <a class="button expanded" href={this.state.live_link} rel="noopener noreferrer" target='_blank'>Live Demo</a>
              <a class="button expanded" href={this.state.github_link} rel="noopener noreferrer" target='_blank'>GitHub Repo</a>
            </div>
          </div>
          <img class="image" alt="" src={process.env.PUBLIC_URL + "/images" + this.state.images[1]}/>
          <img class="image" alt="" src={process.env.PUBLIC_URL + "/images" + this.state.images[2]}/>
          <img class="image" alt="" src={process.env.PUBLIC_URL + "/images" + this.state.images[3]}/>
        </article>
      <nav class= "grid-container portfolio-breadcrumb">
        <a href="/index">← Back</a>
      </nav>
      </div>
      </React.Fragment>
    );
  }
}

export default Project;
