import React, {Component} from 'react';
import data from '../data.json'

class Index extends Component {

  render() {
    return (
      <React.Fragment>
      <div class="off-canvas position-left reveal-for-large portfolio-me" id="my-info" data-off-canvas="">
        <div class="grid-y grid-padding-x" style={{height: '100%'}}><br />
            <div class="cell shrink"><img class="profileThumbnail" src={process.env.PUBLIC_URL + "/images/profile_pic.jpg"} alt="" /></div>
            <div class="cell auto">
                <h5>Judah</h5>
                <p class="text-light">Welcome to my portfolio.</p><a href="/about">Learn More →</a>
            </div>
          </div>
      </div>
      <div class="off-canvas-content" data-off-canvas-content>
        <div class="title-bar hide-for-large">
          <div class="title-bar-left">
            <button class="menu-icon" type="button" data-toggle="my-info"></button>
            <span class="title-bar-title">Judah Devasahayam</span>
          </div>
        </div>
        <article class="grid-container portfolio-intro">
          <div>
            <h1>Hey, Potential Employers!</h1>
            <p class="lead text-light">Here are some great projects I've built.</p>
          </div>
          <hr></hr>
        </article>
        <article class="grid-container portfolio-index">
          <div class="grid-x grid-margin-x small-up-2 medium-up-2 large-up-3">
            {
              data.projects.map((project)=>{
                return <div class="cell" key={project.id}>
                            <a href={'/project/' + project.id}>
                              <img class="imgThumbnail" src={process.env.PUBLIC_URL + "/images" + project.image_urls[0]} alt=""></img>
                              <h5>{project.project_name}</h5>
                            </a>
                        </div>

              })
            }
          </div>
        </article>
      </div>
      </React.Fragment>
    );
  }
}

export default Index;
